::-webkit-scrollbar {
  width: 2px;  /* Remove scrollbar space */
}
/* Optional: show position indicator in red */
::-webkit-scrollbar-thumb {
  background: #FF0000;
}

* {
  margin-top: 0;
  padding: 0;
  box-sizing: border-box;
}


.cursor {
  width: 40px;
  height: 40px;
  border: 2px solid #98e5f7;
  background-color: #E8AB4C;
  border-radius: 100%;
  position: fixed;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  pointer-events: none;
  z-index: 9999;
  mix-blend-mode: difference;
  transition: all 150ms ease;
  transition-property:  opacity, background-color, mix-blend-mode, -webkit-transform;
  transition-property:  opacity, background-color, transform, mix-blend-mode;
  transition-property:  opacity, background-color, transform, mix-blend-mode, -webkit-transform;
}

.cursor--clicked {
  -webkit-transform: translate(-50%, -50%) scale(0.9);
          transform: translate(-50%, -50%) scale(0.9);
  background-color: #fefefe;
}

.cursor--hidden {
  opacity: 0;
}

a:hover {
  color: #E8AB4C;
}

body {
