
  body {
    overflow-x: hidden;
  }

  /* Hide scrollbar for Chrome, Safari and Opera */
.example::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE and Edge */
.example {
  -ms-overflow-style: none;
}

text {
  font-size: 20px;
  color: #A9948B;
}

.counter-btn {
  border-radius: 25px;
  color: white;
  font-family: "Noto Sans TC", sans-serif;
  font-weight: 700;
  font-size: small;
  width: 120px;
  height: 60px;
  margin-top: 30px;
  box-shadow: 0 3px 15px -8px #035ab1;
  background-color: #007fff;
  border: none;
  margin-left: 15px;
  padding: 1%;
  z-index: 9998;
}

.reset-btn {
  border-radius: 25px;
  color: white;
  font-family: "Noto Sans TC", sans-serif;
  font-weight: 700;
  font-size: small;
  width: 120px;
  height: 60px;
  margin-top: 30px;
  box-shadow: 0 3px 15px -8px #FF0000;
  background-color: #FF0000;
  border: none;
  margin-left: 15px;
  padding: 1%;
  z-index: 9998;

}

.reset-btn:hover {
  cursor: none;
  text-decoration: none;
  color: white;
  box-shadow: 0 3px 15px -8px #b80303;
  background-color: #e00000;
}

.counter-btn:hover {
  cursor: none;
  text-decoration: none;
  color: white;
  box-shadow: 0 3px 15px -8px #007fff;
  background-color: #0080ffd8;
}

.btn-wrapper{
  flex-direction: row;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.item-wrapper{
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  z-index: 0;
}
