      }
      .description-container {
        height: 300px;
        width: 350px;
      }
      .projects {
        margin: 0;
        display: flex;
        position: relative;
        justify-content: center;
        align-items: center;
      }
    }

    @media screen and (max-width: 375px) {
      .description {
        font-size: 10px;
        line-height: 25px;
      }

      .description-container {
        height: 300px;
        width: 350px;
      }

      .projects {
        margin: 0;
        display: flex;
        position: relative;
        justify-content: center;
        align-items: center;
      }
    }